body #navbar-collapse nav ul.navbar-nav li a.get-a-quote {
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  text-decoration: none;
  border: 2px solid #fff;
  color: #fff;
  display: inline-block;
  max-width: 100%;
  background: transparent;
  font-size: 16px;
  font-family: "Roboto";
  font-weight: 700;
  border-radius: 4px;
  padding: 18px 30px;
  -webkit-box-shadow: inset 0px 0px 0px 1px #fff;
  -moz-box-shadow: inset 0px 0px 0px 1px #fff;
  box-shadow: inset 0px 0px 0px 1px #fff;
  text-transform: uppercase;
  transition: color 0.1s ease-in-out, background 0.1s ease-in-out, border 0.1s ease-in-out;
  -webkit-transition: color 0.1s ease-in-out, background 0.1s ease-in-out, border 0.1s ease-in-out;
  border-color: #041e41;
  padding: 8px 15px;
  color: #041e41;
  width: max-content;
  margin-inline: auto;
}
body #navbar-collapse nav ul.navbar-nav li a.get-a-quote:hover span {
  color: #00aeef;
  -webkit-text-fill-color: #00aeef;
}
body #navbar-collapse nav ul.navbar-nav li a.get-a-quote:hover::after {
  width: 100%;
  height: 4000%;
  opacity: 1;
}
body #navbar-collapse nav ul.navbar-nav li a.get-a-quote span {
  position: relative;
  z-index: 2;
}
body #navbar-collapse nav ul.navbar-nav li a.get-a-quote::after {
  content: "";
  position: absolute;
  transition: all 0.2s ease-out;
  -webkit-transition: all 0.2s ease-out;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%) rotate(45deg) translateZ(0);
  -webkit-transform: translateX(-50%) translateY(-50%) rotate(45deg) translateZ(0);
  -ms-transform: translateX(-50%) translateY(-50%) rotate(45deg) translateZ(0);
  height: 0;
  width: 0;
  opacity: 0;
  background-color: #fff;
}
body #navbar-collapse nav ul.navbar-nav li a.get-a-quote:hover {
  border-color: #041e41;
}
body #navbar-collapse nav ul.navbar-nav li a.get-a-quote:hover span {
  color: #fff;
  -webkit-text-fill-color: #fff;
}
body #navbar-collapse nav ul.navbar-nav li a.get-a-quote::after {
  background-color: #041e41;
}
